import returns from '../common/returns.vue'
export default{
	components:{
		returns
	},
	data(){
		return{
			title:'立即报名',
			name:'',
			phone:''
		}
	},
	methods:{
		 register(){
			  let tel = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(this.phone);
			   if (this.phone == "" || this.name == "") {
				  uni.showToast({
				  	icon:'none',
				  	title:'输入框不能为空!'
				  })
			  }else if(!tel){
				  uni.showToast({
				  	icon:'none',
				  	title:'请输入正确的11位手机号码!'
				  })
			  }
		 }
	}
}
